.catalog-item { position: relative; display: flex; justify-content: flex-start; align-items: center; font-size: 16px; padding-top: 30px; padding-bottom: 30px; font-family: "FiraSans-Regular", Arial, sans-serif; color: #282828; transition: box-shadow 0.3s; margin-left: -20px; margin-right: -20px; padding-left: 20px; padding-right: 20px; }
